@import url('./normolize.css');
@import url('../fonts/icomoon/style.css');
@import url('../fonts/icomoon/fonts/button/button_icon_style.css');
@import url('./fonts.css');

html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	max-width: 1200px;
	margin: 0 auto;

	white-space: nowrap;

	color: #172B4D;
	background:linear-gradient(8deg, rgba(11,23,95,0.9028653697807247) 25%, rgba(26,210,173,0.35384576193758754) 88%);
	height: 100vh;

	font-family: 'GoogleSans', Arial, Helvetica, sans-serif;
	font-size: 17px;
	font-weight: 400;
}